#body{margin: 0}
.header{
	position:absolute;
	margin-left:auto;
	margin-right:auto;
	width: 99.4%;
}
.header_content{
	position:absolute;
	margin-left:auto;
	margin-right:auto;
	width:70%;
}

.metro_blog{
	position:absolute;
	margin-left:30%;
	width:50%;
	font-family: 'Cabin', Medium;
	font-size: 2.5em;
	color: #595959;
	float:right;
}
.circle_logo{
	position: static;
	width:0.6em;
	vertical-align: middle;
}
.menu{
	position:absolute;
	margin-left:60%;
	width:50%;
	text-align:right;
	margin-top:1%;
}
.menu_link{
	text-decoration:none;
	font-size: 1em;
	color: #313131;
	margin-left:2%;
	font-family: 'ABeeZee', sans-serif;
	background-color: white;
    padding: 7px 17px;
    text-align: center;
    display: inline-block;
	
}
.menu_link:hover{
text-decoration:none;
	font-size: 1em;
	color: #ffffff;
	margin-left:2%;
	
	background-color: #ee1551;
    padding: 7px 17px;
    text-align: center;
    display: inline-block;
	
}

.first_content{
	position:absolute;
	margin-left:auto;
	margin-right:auto;
	width: 99.4%;
	margin-top:6%;
}
.video{
	position:absolute;
	margin-left:30%;
	width:40%;
	font-family: 'Open Sans';
	font-size: 3em;
	color: #5d5d5d;
	float:right;
	margin-top:15%;
	line-height:100%;
}
.video_link{
	text-decoration:none;
	font-size: 0.4em;
	color: #ffffff;
	
	background-color: #ee1551;
    padding: 0.1px 10px;
    text-align: center;
    display: inline-block;
}
.iPad_image{
	position:absolute;
	margin-left:60%;
	width:60%;
	text-align:right;
	margin-top:9%;
}
